.why-trade {
  height: 603px;
  background: #F8F8F8;
  color: #000;
  font-family: 'Open Sans', sans-serif;
}

.why-trade-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

.why-trade__title {
  padding: 144px 0 65px;
  font-weight: 700;
  font-size: 44px;
  line-height: 60px;
  text-align: center;
}

.why-trade__subsubtitle {
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  padding: 34px 0 8px;
}

.why-trade__img {
  width: 55px;
  height: 55px;
}

.why-trade__img--1 {
  background: url('./img/global-market.png');
}

.why-trade__img--2 {
  background: url('./img/powerful-platforms.png');
}

.why-trade__img--3 {
  background: url('./img/industry-leading.png');
}

.why-trade__img--4 {
  background: url('./img/export-support.png');
}



.why-trade__text {
  font-size: 14px;
  line-height: 21px;
  text-align: center;
}

@media (max-width: 991px) {
  .why-trade {
    height: 700px;
  }
  
  .why-trade__title {
    padding-top: 70px;
    font-size: 38px;
  }

  .why-trade__subsubtitle {
    padding-top: 14px;
  }
}

@media (max-width: 767px) {
  .why-trade {
    height: 700px;
  }
  
  .why-trade__title {
    padding: 40px 0;
    font-size: 32px;
  }

  .why-trade__subsubtitle {
    font-size: 16px;
  }

  .why-trade__text {
    font-size: 12px;
  }
}

@media (max-width: 575px) {
  .why-trade {
    height: 660px;
  }

  .why-trade__subsubtitle {
    font-size: 14px;
  }

  .why-trade__text {
    font-size: 11px;
  }
}

@media (max-width: 523px) {
  .why-trade {
    height: 720px;
  }
}

@media (max-width: 500px) {
  .why-trade {
    height: 740px;
  }
}

@media (max-width: 418px) {
  .why-trade {
    height: 780px;
  }
}

@media (max-width: 396px) {
  .why-trade {
    height: 830px;
  }

  .why-trade__subsubtitle {
    height: 76px;
    display: flex;
    align-items: center;
  }
}

@media (max-width: 368px) {
  .why-trade {
    height: 860px;
  }
}

@media (max-width: 345px) {
  .why-trade {
    height: 880px;
  }
}

@media (max-width: 330px) {
  .why-trade {
    height: 900px;
  }
}